import { errors } from '@telerik/kendo-intl';

const DOCS_URL = 'http://www.telerik.com/kendo-angular-ui/components/internationalization/troubleshooting/';

/* tslint:disable:variable-name */
const { NoLocale, NoCurrencyDisplay } = errors;

const local_errors: { [x: string]: string; } = {
    'NoLocale': `${NoLocale.message} Solution: ${DOCS_URL}#missing-cldr-info`,
    'NoCurrencyDisplay': `${NoCurrencyDisplay.message} Solution: ${DOCS_URL}#insufficient-default-locale`
};

export { local_errors as errors };



